*, :before, :after {
  box-sizing: border-box;
}

.main {
  color: #2f4f4f;
  width: 100%;
  font-family: Arial, sans-serif;
  font-size: 20px;
  font-weight: 700;
}

.person {
  grid-template-columns: repeat(2, 200px);
  justify-content: center;
  align-items: center;
  gap: 0 30px;
  margin-top: 30px;
  display: grid;
}

@media (width >= 1000px) {
  .person {
    grid-template-columns: repeat(4, 200px);
  }
}

.person__item {
  height: 270px;
  padding: 0;
  transition: transform 1s, border-radius 1s;
}

.person__photo {
  display: flex;
  position: relative;
}

.person__img {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  transition: all .3s;
}

.person__rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 16px;
  display: flex;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translate(-40%, -50%);
}

.person__details {
  text-align: center;
  padding-top: 8px;
}

.person__extra {
  color: #000;
  padding-top: 10.8px;
  font-size: 21.6px;
  font-weight: 400;
  display: none;
}

.person__item:hover {
  z-index: 1;
  background-color: #eee;
  border-radius: 5px;
  transform: scale(1.3);
  box-shadow: 0 0 0 12px #eee;
}

.person__item:hover .person__extra {
  display: block;
}

.person__item:hover .person__img {
  border-radius: 60px;
}
